.neon-main {
  border-radius: 15px;
  border-style: solid;
  border-width: 4px;
  filter: blur(2px);
  height: 80%;
  opacity: 0;
  transition: all 2s;
  width: 80%;
}

.neon-main-enter {
    opacity: 0;
  }

.neon-main-enter-done {
    opacity: 1;
  }

.neon-main-exit {
    opacity: 1;
  }

.neon-main-exit-done {
    opacity: 0;
  }

.neon-white {
  border-color: #fff;
  border-radius: 15px;
  border-style: solid;
  border-width: 2px;
  filter: blur(1px);
  height: 76%;
  opacity: 0;
  transition: all 2s;
  width: 76%;
}

.neon-white-enter {
    opacity: 0;
  }

.neon-white-enter-done {
    opacity: .8;
  }

.neon-white-exit {
    opacity: .8;
  }

.neon-white-exit-done {
    opacity: 0;
    transition: opacity 4s ease-out;
  }

.neon-aura {
  border-radius: 15px;
  border-style: solid;
  border-width: 6px;
  filter: blur(10px);
  height: 80%;
  opacity: 0;
  transition: all 4s;
  width: 80%;
}

.neon-aura-enter {
    opacity: 0;
  }

.neon-aura-enter-done {
    opacity: .6;
  }

.neon-aura-exit {
    opacity: .6;
  }

.neon-aura-exit-done {
    opacity: 0;
  }

